.EventCard {
  width: 350px;
  height: 450px;
  border-radius: 3px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}
.EventCard .thumb {
  width: auto;
  height: 320px;
  /* background: url("https://i0.wp.com/wp.laravel-news.com/wp-content/uploads/2019/10/hacktoberfest.jpg?fit=2200%2C1100&ssl=1?resize=2200%2C1125") ; */
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 3px;
}
.EventCard .infos {
  width: auto;
  height: 450px;
  position: relative;
  padding: 5px 24px;
  background: #fff;
  -webkit-transition: 0.4s 0.15s cubic-bezier(0.17, 0.67, 0.5, 1.03);
  transition: 0.4s 0.15s cubic-bezier(0.17, 0.67, 0.5, 1.03);
}
.EventCard .infos .title {
  position: relative;
  margin: 7px 0;
  letter-spacing: 3px;
  color: #152536;
  font-family: "Grotesque Black", sans-serif;
  font-size: 1rem;
  text-transform: uppercase;
  text-shadow: 0 0 0px #32577f;
}
.EventCard .infos .time {
  position: absolute;
  top: 50%;
  right: 0;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 35px;
  height: 23px;
  font-size: 0.9rem;
  color: rgba(67, 83, 100, 0.7);
  font-family: "Grotesque", sans-serif;
  background-size: 100% auto;
  display: inline-block;
}
.EventCard .infos .detail {
  margin-bottom: 10px;
  text-transform: uppercase;
  font-size: 0.85rem;
  color: rgba(21, 37, 54, 0.7);
  font-family: "Grotesque", sans-serif;
}
.EventCard .infos .txt {
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  padding-top: 13px;
  font-family: "Merriweather", sans-serif;
  line-height: 2;
  font-size: 0.95rem;
  color: rgba(21, 37, 54, 0.7);
  opacity: 0;
  -webkit-transition: 0.5s 0.25s cubic-bezier(0.17, 0.67, 0.5, 1.03);
  transition: 0.5s 0.25s cubic-bezier(0.17, 0.67, 0.5, 1.03);
}
.EventCard .infos .details {
  position: absolute;
  left: 0;
  left: 0;
  bottom: 0;
  margin: 10px 0;
  padding: 20px 24px;
  letter-spacing: 1px;
  color: #4e958b;
  font-family: "Grotesque Black", sans-serif;
  font-size: 0.9rem;
  text-transform: uppercase;
  cursor: pointer;
  opacity: 0;
  -webkit-transition: 0.5s 0.25s cubic-bezier(0.17, 0.67, 0.5, 1.03);
  transition: 0.5s 0.25s cubic-bezier(0.17, 0.67, 0.5, 1.03);
}
.EventCard:hover .infos {
  -webkit-transform: translateY(-320px);
  transform: translateY(-320px);
}
.EventCard:hover .infos .seats,
.EventCard:hover .infos .txt,
.EventCard:hover .infos .details {
  opacity: 1;
}
